/* -------- VAR -------- */
:root {
    --rowHeight: 20px;
    --timelineHeight: 40px;
}

/*begin css reset*/
body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/*end css reset*/

body {
    margin: 0px;
    // font-family: 'Segoe UI Semibold',wf_segoe-ui_semibold,helvetica,arial,sans-serif;
}

/* -------- DIV -------- */

div {
    border: 1px dashed black;
    padding: 0px;
    margin: 0px;
    width: 100%;
}

#div-header {
    // height: 10%;
    min-height: 30px;

    border-style: none none solid none;
    border-width: 1px;
    border-color: black;
}

#div-content {
    height: fit-content;
    display: flex;
    justify-content: flex-end;

}

#div-tasks {
    width: 25%;
}

#div-chartContainer {
    flex-grow: 1;

    display: grid;
    grid-template-columns: 1fr;
    border-style: none none none solid;
    border-width: 2px;
    border-color: black;

    height: 500px;
}

//https://ishadeed.com/article/less-absolute-positioning-modern-css/
//use grids, not absolutes!

.gridStack {
    //grid-area: 1 / 2; //can '2' be dynamic? Will fail if  2 =/= no. of grid items.
    //didnt get the above syntax to work...longhand it is.
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
}

#div-structureLayer {
    height: 100%;
    display: flex;
    flex-direction: column;
    height: fit-content;
}

#div-svgLayer {
    height: 100%;
    z-index: 1;
}

