#div-timeline {
    height: var(--timelineHeight);

}

#div-chart {
    height: fit-content;
    min-height: 150px;
    flex-grow: 1;
    position: relative;
}

/* ------- TABLE ------- */

table {
    width: 100%;
}

th {
    height: var(--timelineHeight);
}

tr {
    height: var(--rowHeight);
}

td {
    border: 1px #aaaaaa;
    border-style: solid none solid solid;
}

