/* ------- TIMELINE ------- */

#svg-statusLine {
    z-index: 3;
}

#statusLine {
    stroke: rgb(255, 0, 0);
    stroke-width: 2px;
}



/* ------- DEBUG ------- */

.highlight {
    border: 1px solid #00ff00;
    background-color: rgba(0, 255, 0, 0.25);
}